.list{
    width: 100%;
    margin-top: 10px;

    .listTitle{
        color: white;
        font-size: 20px;
        font-weight: 500;
        margin-left: 50px;
    }

    .wrapper{
      position: relative;

        .sliderArrow{
            width: 50px;
            height: 50px;
            color: white;
            background-color: rgb(95, 94, 94);
            opacity: 0.7;
            position: absolute;
            z-index: 999;
            top: 0;
            bottom: 0;
            margin: auto;
            cursor: pointer;

            &.left{
                left: 0;
            }

            &.right{
                right: 0;
            }
        }
        
        .container{
            margin-left: 50px;
            display: flex;
            width: max-content;
            margin-top: 10px;
            // transform: translateX(0px);
            transition: all 1s ease;
        }
    }
}